.wrapper {
    overflow: auto;
    max-height: 65vh;
   &::-webkit-scrollbar {
        width: 0.2em;
      }
    &::-webkit-scrollbar-track {
        box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    }
    &::-webkit-scrollbar-thumb {
        background-color: rgb(98, 112, 121);
        outline: 1px solid rgb(39, 45, 51);
      }
      &Deactive {
          overflow: hidden;
      }
}


